/**
 * 使用方法
 * 1.在main.js中引入
 * import $validate from './assets/js/validate'
 * Vue.prototype.$validate = $validate
 * 2.页面使用 this.$validate.方法名(参数)
 */
 export default {
  filter_rules(item) {
    /** @description element form表单验证
     * @property {funtion} filter_rules   判断验证方法
     * @property {Boolean} required = [true|false]    是否是必填项
     * @property {String} type = [blank(判断不能为空或者空格)|money(验证两位小数金额)|mobile(验证手机号)|email(验证邮箱)|num(验证正整数)]
     * @property {String} tips   提示词
     * @property {Boolean} select = [true|false]    是否是必选项
     * type的验证方式 :rules="$validate.filter_rules({required:true,type:'blank',value:patrolTask.patrolId,tips:'巡更任务'})"
     * select的验证方式 :rules="$validate.filter_rules({select:true,tips:'巡更路线'})"
     */
    const rules = [];
    if (item.required) {
      rules.push({
        required: true,
        message: "请输入" + item.tips,
        trigger: ['blur',"change"]
      });
    }
    if (item.select) {
      rules.push({ required: true, message: "请选择" + item.tips });
    }
    if (item.maxLength) {
      rules.push({
        min: 1,
        max: item.maxLength,
        message: "最多输入" + item.maxLength + "个字符!",
        trigger: "blur"
      });
    }
    if (item.min && item.max) {
      rules.push({
        min: item.min,
        max: item.max,
        message:
          item.tips + "长度在" + item.min + "位至" + item.max + "位之间!",
        trigger: "blur"
      });
    }
    if (item.type) {
      const type = item.type;
      switch (type) {
        case "num":
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (!/(^[0-9]\d*$)/.test(item.value)) {
                callback(new Error("请输入数字类型的" + item.tips));
              } else {
                callback();
              }
            }
          });
          break;
        case "email":
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (
                !/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(
                  item.value
                )
              ) {
                callback(new Error("请输入正确的邮箱"));
              } else {
                callback();
              }
            }
          });
          break;
        case "mobile": // 验证手机号
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (!/^1[3456789]\d{9}$/.test(item.value)) {
                callback(new Error("请输入正确的手机号"));
              } else {
                callback();
              }
            }
          });
          break;
        case "UDI": // 验证手机号
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (!/^[0-9()]{30}$/.test(item.value)) {
                callback(
                  new Error("UDI只能由数字和英文格式的括号组成的30位字符")
                );
              } else {
                callback();
              }
            }
          });
          break;
        case "id": // 验证身份证号
          rules.push({
            validator: function(rule, value, callback) {
              if (
                !/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(
                  item.value
                )
              ) {
                callback(new Error("请输入正确的身份证号"));
              } else {
                callback();
              }
            },
            trigger: "blur,change"
          });
          break;
        case "chinese": // 验证汉字类型
          rules.push({
            trigger: ["blur", "change"],
            validator: function(rule, value, callback) {
              if (/[^\u4E00-\u9FA5]/g.test(item.value)) {
                callback(new Error("请输入汉字类型" + item.tips));
              } else {
                callback();
              }
            }
          });
          break;
        case "noChinese": // 验证汉字类型
          rules.push({
            trigger: ["blur", "change"],
            validator: function(rule, value, callback) {
              if (/[\u4E00-\u9FA5]/g.test(item.value)) {
                callback(new Error(item.tips + "不能包含汉字"));
              } else {
                callback();
              }
            }
          });
          break;
        case "money": // 验证两位小数金额
          rules.push({
            trigger: ["blur", "change"],
            validator: function(rule, value, callback) {
              if (
                !/(^(([1-9]([0-9]+)?)|(0{1}))(\.[0-9]{1,2})?$)/.test(item.value)
              ) {
                callback(new Error("请输入两位小数的" + item.tips));
              } else {
                callback();
              }
            }
          });
          break;
        case "blank": // 验证空格为空
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (item.value !== undefined && item.value.trim() === "") {
                callback(new Error("请输入非空格数据"));
              } else {
                callback();
              }
            }
          });
          break;
        default:
          rules.push({});
          break;
      }
    }
    return rules;
  },
  pickerOptions(type, date) {
    if (type === "aftertoday") {
      // 今天和以后
      return { disabledDate(time) { return time.getTime() < Date.now() - 8.64e7 } }
    } else if (type === "beforetoday") {
      // 今天和以前
      return { disabledDate(time) { return time.getTime() > Date.now() - 8.64e6 } }
    } else if (type === "beforetoday") {
      // 明天和以后
      return { disabledDate(time) { return time.getTime() < Date.now() } }
    } else if (type === "beforetoday") {
      // 昨天和以前
      return { disabledDate(time) { return time.getTime() > Date.now() } }
    } else if (type === "60") {
      // 60天之前到现在
      return {
        disabledDate(time) {
          const curDate = (new Date()).getTime();
          const three = 90 * 24 * 3600 * 1000;
          const threeMonths = curDate - three;
          return time.getTime() > Date.now() || time.getTime() < threeMonths;
        }
      }
    } else if (type === "6") {
      // 60天之前到现在
      return {
        disabledDate(time) {
          const curDate = (new Date()).getTime();
          const three = 90 * 24 * 3600 * 1000;
          const threeMonths = curDate - three;
          //  return time.getTime() < this.value1 || time.getTime() > Date.now();
          return time.getTime() > Date.now() || time.getTime() < threeMonths;
        }
      }
    } else if (type === "startData") {
      return {
        disabledDate: time => {
          if (date) { // 必须晚于结束日期   好像没实现
            return (
              time.getTime() > new Date(date).getTime()
            )
          }
        }
      }
    } else if (type === "endDate") {
      return {
        disabledDate: time => { //  不能早于开始日期
          if (date) {
            return time.getTime() < new Date(date).getTime();
          }
        }
      }
    }
  },
  timeverify(type, data) {
    /** @description 时间框验证
     * @property {funtion} timeverify(type,data)   判断验证方法
     *  type=0 只能选择大于等于当前时间
     *  type=1 只能选择小于等于当前时间
     *  type=3 data!="" 只能选择d等于当前时间
     *  type=0 只能选择大于等于当前时间
     */
    var validityPickerOptions = {};
    if (type === 0) {
      validityPickerOptions = {
        disabledDate: time => {
          return time.getTime() < Date.now() - 8.64e7;
        }
      };
    } else if (type === 1) {
      validityPickerOptions = {
        disabledDate: time => {
          return time.getTime() > Date.now();
        }
      };
    }
    return validityPickerOptions;
  },
  formatDate(fmt) {
    /** @description 格式化当前日期
     * @property {funtion} formatDate(fmt)   判断验证方法
     *  fmt="yyyy-MM-dd hh:mm:ss"
     */
    var myDate = new Date();
    var o = {
      "M+": myDate.getMonth() + 1, // 月份
      "d+": myDate.getDate(), // 日
      "h+": myDate.getHours(), // 小时
      "m+": myDate.getMinutes(), // 分
      "s+": myDate.getSeconds(), // 秒
      "q+": Math.floor((myDate.getMonth() + 3) / 3), // 季度
      S: myDate.getMilliseconds() // 毫秒
    };
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (myDate.getFullYear() + "").substr(4 - RegExp.$1.length)
      );
    }
    for (var k in o) {
      if (new RegExp("(" + k + ")").test(fmt))
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ("00" + o[k]).substr(("" + o[k]).length)
        );
    }
    return fmt;
  }
};


/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}
